*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  background-color: #000;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.header__logo {
  padding: 20px 10px 20px 5px;
}

.lang {
  height: 34px;
  padding: 5px 9px;
  line-height: 9px;
  font-size: 16px;
  letter-spacing: 2px;
  background-color: #000;
  color: #fff;
}

.main-wrap {
  display: flex;
  flex-direction: row;
  border-top: solid 1px #828282;
  border-bottom: solid 1px #828282;
  position: relative;
}

.nation-wrap,
.type-wrap {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  width: 180px;
  padding: 20px;
}

.type-wrap {
  border-left: solid 1px #828282;
  border-right: solid 1px #828282;
}

.nation-title,
.type-title {
  color: #fff;
  margin-bottom: 20px;
  font-weight: bold;
  text-decoration: underline;
  font-size: 20px;
}

.nation-list,
.type-list {
  isplay: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  list-style: none;
  font-size: 15px;
  color: #fff;
  width: 120px;
}

.nation-item,
.type-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  cursor: pointer;    
  margin: 10px;
  padding: 7px 1px;
}

.nation-item--active,
.type-item--active {
  box-shadow: 0px 0px 10px 5px #cc2331;
}

.nation-item:hover,
.type-item:hover {
  box-shadow: 0px 0px 10px 5px #63b524;
}

.nation-img {
  height: 26px;
  width: 41px;
  margin-bottom: 5px;
}

.type-img {
  height: 75px;
  width: 140px;
}

.vehicle-wrap {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.vehicle-item {
  position: relative;
  height: 100px;
  margin-bottom: 15px;
}

.vehicle-type {
  position: absolute;
  left: 0;
  top: 1px;
  background: url(../images/types/types.png);
  z-index: 2;
  width: 24px;
  height: 23px;
  padding-left: 24px;
  padding-top: 2px;
  color: #bdbaac;
}

.type-heavyTank {
  background-position: 0 -275px;
}

.type-heavyTank--premium {
  background-position: 0 -45px;
}

.type-AT-SPG {
  background-position: 0 -297px;
}

.type-AT-SPG--premium {
  background-position: 0 -69px;
}

.type-mediumTank {
  background-position: 0 -252px;
}

.type-mediumTank--premium {
  background-position: 0 -23px;
}

.type-lightTank {
  background-position: 0 -229px;
}

.type-lightTank--premium {
  background-position: 0 0;
}

.type-SPG {
  background-position: 0 -320px;
}

.type-SPG--premium {
  background-position: 0 -92px;
}

.vehicle-flag {
  width: 160px;
  height: 102px;
  background: url(../images/flags/flags.png) no-repeat;
  position: absolute;
  left: 0;
  top: 0;
}

.vehicle-flag-italy {
  background-position: 0 -900px;
}

.vehicle-flag-usa {
  background-position: 0 0;
}

.vehicle-flag-czech {
  background-position: 0 -700px;
}

.vehicle-flag-poland {
  background-position: 0 -1000px;
}

.vehicle-flag-france {
  background-position: 0 -300px;
}

.vehicle-flag-sweden {
  background-position: 0 -800px;
}

.vehicle-flag-ussr {
  background-position: 0 -100px;
}

.vehicle-flag-china {
  background-position: 0 -200px;
}

.vehicle-flag-uk {
  background-position: 0 -500px;
}

.vehicle-flag-japan {
  background-position: 0 -400px;
}

.vehicle-flag-germany {
  background-position: 0 -600px;
}

.vehicle-img {
  z-index: 4;
  opacity: 0.8;
}

.vehicle-title {
  position: absolute;
  left: 0px;
  bottom: 0;
  background-color: #6766665c;
  color: #bdbaac;
  width: 100%;
  font-size: 15px;
  padding-left: 3px;
  border-top: solid 1px #696767;
}

.vehicle-title--premium {
  color: #fe881f;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
  color: #fff;
  z-index: 1072;
  background-color: #7b7b7bd1;
}

.modal--active {
  display: block;
  overflow-x: hidden;
  overflow-y: auto;
}

.modal-wrap {
  margin: 9.75rem auto;
  width: auto;
  max-width: 515px;
  border: solid 5px #000;
  border-radius: 10px;
  padding: 40px;
  background-color: #000;
}


.footer {
  position: relative;
}

.footer::after {
  content: '';
  background-image: url(../images/page/bg-img.png);
  background-repeat: no-repeat;
  position: absolute;
  left: 0px;
  top: -100px;
  height: 300px;
  width: 250px;
}

.footer-list {
  margin: 0px 10px 0 300px;
  display: flex;
  flex-direction: column;
  padding: 50px;
}

.footer-item {
  margin-bottom: 15px;
}

.footer-link {
  text-decoration: none;
  color: #fff;
  position: relative;
  padding-left: 30px;
}

.footer-link::before {
  content: '';
  position: absolute;
  background-repeat: no-repeat;
  height: 24px;
  width: 24px;
  left: 0;
  top: -3px;
}

.github::before {
  background-image: url(../images/page/github.svg);
}

.wot::before {
  background-image: url(../images/page/logo.svg);
}

.wargaming::before {
  background-image: url(../images/page/wargaming.svg);
}
